.post-enter {
    transform: translateX(-350px);
}
.post-enter-active {
    transform: translateX(0px);
    transition: all 300ms, transform 300ms;
}
.post-exit {
    opacity: 1;
}
.post-exit-active {
    transform: translateX(-350px);
    transition: all 300ms, transform 300ms;
}

.posts{
    padding: 20px;
    margin-top: 10px;
}

.pageWrapper{
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(auto-fill, 40px);
    grid-template-rows: 40px 40px;
    justify-items: stretch;
    align-items: stretch;
}
.page{
    margin: 2px;
    padding: 5px;
    border: 1px solid teal;
    cursor: pointer;
    display: block;
}

.pageCurrent{
    border: 2px solid red;
    font-weight: bold;
}
